@import "fonts";
html,
body {
    height: 100%;
}

body {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 18px;
    line-height: 27px;
    color: #969696;
    min-width: 280px;
}

img {
    max-width: 100%;
    height: auto;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

.main {
    flex: 1 1 auto;
}

.container {
    max-width: 1160px;
    padding: 0 10px;
    margin: 0 auto;
}

.section {
    padding: 80px 0;
}

.logo {
    margin-right: 15px;
}

.button {
    display: inline-block;
    font-family: "Inter";
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #141e32;
    text-align: center;
    border-radius: 10px;
    min-width: 170px;
    padding: 13px;
}

.button-primary {
    color: #ffffff;
    border: 1px solid #545be8;
    background-color: #545be8;
    transition: color 0.3s ease, background-color 0.3s ease;
}

.button-primary:hover {
    color: #545be8;
    background: #ffffff;
}

.button-accent {
    color: #545be8;
    border: 1px solid #545be8;
    background-color: #ffffff;
    transition: color 0.3s ease, background-color 0.3s ease;
}

.button-accent:hover {
    color: #ffffff;
    background-color: #545be8;
}

.title {
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    color: #141e32;
    max-width: 291px;
    margin: 16px 0;
}

.suptitle {
    font-family: 'Inter', sans-serif;
    line-height: 22px;
    text-transform: uppercase;
}

.socials__list {
    display: flex;
    gap: 16px;
}

.socials__item:hover {
    transform: scale(1.1);
}

.decorate__images {
    position: relative;
}

.decorate__image {
    position: absolute;
}
