.footer {
    padding: 91px 0 107px;
    background-color: #393e46;
}

.footer__inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.footer__left {
    max-width: 424px;
    margin-right: 25px;
}

.footer__logo {
    display: inline-block;
    margin-bottom: 24px;
}

.footer__text {
    font-size: 14px;
    line-height: 24px;
    color: #f4f4f4;
    margin-bottom: 40px;
}

.footer__right {
    display: flex;
    gap: 80px;
}

.footer__nav-title {
    font-weight: 700;
    color: #ffffff;
    margin-bottom: 24px;
}

.footer__nav-item {
    margin-bottom: 11px;
}

.footer__nav-link {
    font-size: 14px;
    line-height: 21px;
    color: #f4f4f4;
    padding-bottom: 1px;
    border-bottom: 1px solid transparent;
}

.footer__nav-link:hover {
    border-bottom: 1px solid #ffffff;
}
