.reviews__container {
    max-width: 1200px;
    margin: 0 auto;
}

.reviews__title {
    font-family: "Mada", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 52px;
    text-align: center;
    color: #0d1222;
    margin-bottom: 40px;
}

.reviews__list {
    display: flex;
    gap: 24px;
    flex-wrap: wrap;
}

.reviews__item {
    max-width: 588px;
    padding: 40px;
    background-color: #ffffff;
    box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.1);
    position: relative;
}

.reviews__item::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    border: 39px solid transparent;
    border-top: 39px solid #f4f4f4;
    border-right: 39px solid #f4f4f4;
}

.reviews__user-desc {
    margin-bottom: 24px;
}

.user-desc {
    display: flex;
}

.user-desc__image {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin-right: 16px;
}

.user-desc__title {
    font-weight: 700;
    color: #000000;
    margin-bottom: 8px;
}

.user-desc__status {
    font-size: 14px;
    line-height: 21px;
    color: #969696;
}

.reviews__stars {
    display: flex;
    gap: 8px;
    margin-bottom: 24px;
}

.reviews__star svg path {
    fill: #f1cc4a;
}

.reviews__text {
    line-height: 30px;
    color: #646464;
}

.reviews__text::after {
    content: "“";
}
.reviews__text::before {
    content: "“";
}
