.header {
    background-color: #ffffff;
    padding: 16px 0;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
}

.header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__nav {
    display: flex;
    align-items: center;
    gap: 46px;
}

.header__nav-list {
    display: flex;
    gap: 40px;
}

.header__nav-item a {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #101928;
    transition: text-shadow 0.1s ease;
}

.header__nav-item:hover a {
    text-shadow: 0 0 0.65px #101928, 0 0 0.65px #101928;
}

.header__buttons {
    display: flex;
    gap: 24px;
}

