.hero__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.hero__content {
    max-width: 560px;
}

.hero__suptitle {
    color: #545be8;
    margin-bottom: 16px;
    text-transform: unset;
}

.hero__suptitle span {
    font-weight: 700;
}

.hero__title {
    font-weight: 700;
    font-size: 64px;
    line-height: 96px;
    color: #141e32;
    margin-bottom: 24px;
}

.hero__title span {
    color: #545be8;
}

.hero__text {
    margin-bottom: 40px;
}

.hero__buttons {
    display: flex;
    gap: 24px;
}

.button-play {
    background-image: url('./resourse/images/icons/play-icon.svg');
    background-repeat: no-repeat;
    background-size: 32px 32px;
    background-position-y: center;
    background-position-x: 18px;
    padding: 13px 18px 13px 58px;
}

.hero__image-1 {
    top: -36px;
    left: 0;
}

.hero__image-2 {
    top: 2px;
    right: 103px;
}

.hero__image-2 svg circle {
    fill: #FE753F;
}

.hero__image-3 {
    right: 24px;
    bottom: -8px;
}

.hero__image-3 svg circle {
    fill: #058FCB;
}

.hero__image-4 {
    bottom: -24px;
    left: 0;
}

.hero__image-5 {
    left: 156px;
    bottom: -40px;
}

.hero__image-6 {
    left: -63px;
    bottom: 22px;
}