html,
body {
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  line-height: 27px;
  color: #969696;
  min-width: 280px;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 1 auto;
}

.container {
  max-width: 1160px;
  padding: 0 10px;
  margin: 0 auto;
}

.section {
  padding: 80px 0;
}

.logo {
  margin-right: 15px;
}

.button {
  display: inline-block;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #141e32;
  text-align: center;
  border-radius: 10px;
  min-width: 170px;
  padding: 13px;
}

.button-primary {
  color: #ffffff;
  border: 1px solid #545be8;
  background-color: #545be8;
  transition: color 0.3s ease, background-color 0.3s ease;
}

.button-primary:hover {
  color: #545be8;
  background: #ffffff;
}

.button-accent {
  color: #545be8;
  border: 1px solid #545be8;
  background-color: #ffffff;
  transition: color 0.3s ease, background-color 0.3s ease;
}

.button-accent:hover {
  color: #ffffff;
  background-color: #545be8;
}

.title {
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  color: #141e32;
  max-width: 291px;
  margin: 16px 0;
}

.suptitle {
  font-family: "Inter", sans-serif;
  line-height: 22px;
  text-transform: uppercase;
}

.socials__list {
  display: flex;
  gap: 16px;
}

.socials__item:hover {
  transform: scale(1.1);
}

.decorate__images {
  position: relative;
}

.decorate__image {
  position: absolute;
}

.header {
  background-color: #ffffff;
  padding: 16px 0;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__nav {
  display: flex;
  align-items: center;
  gap: 46px;
}

.header__nav-list {
  display: flex;
  gap: 40px;
}

.header__nav-item a {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #101928;
  transition: text-shadow 0.1s ease;
}

.header__nav-item:hover a {
  text-shadow: 0 0 0.65px #101928, 0 0 0.65px #101928;
}

.header__buttons {
  display: flex;
  gap: 24px;
}

.hero__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero__content {
  max-width: 560px;
}

.hero__suptitle {
  color: #545be8;
  margin-bottom: 16px;
  text-transform: unset;
}

.hero__suptitle span {
  font-weight: 700;
}

.hero__title {
  font-weight: 700;
  font-size: 64px;
  line-height: 96px;
  color: #141e32;
  margin-bottom: 24px;
}

.hero__title span {
  color: #545be8;
}

.hero__text {
  margin-bottom: 40px;
}

.hero__buttons {
  display: flex;
  gap: 24px;
}

.button-play {
  background-image: url("./resourse/images/icons/play-icon.svg");
  background-repeat: no-repeat;
  background-size: 32px 32px;
  background-position-y: center;
  background-position-x: 18px;
  padding: 13px 18px 13px 58px;
}

.hero__image-1 {
  top: -36px;
  left: 0;
}

.hero__image-2 {
  top: 2px;
  right: 103px;
}

.hero__image-2 svg circle {
  fill: #FE753F;
}

.hero__image-3 {
  right: 24px;
  bottom: -8px;
}

.hero__image-3 svg circle {
  fill: #058FCB;
}

.hero__image-4 {
  bottom: -24px;
  left: 0;
}

.hero__image-5 {
  left: 156px;
  bottom: -40px;
}

.hero__image-6 {
  left: -63px;
  bottom: 22px;
}

.give {
  padding-left: 150px;
}

.give__inner {
  display: flex;
  gap: 24px;
}

.give__content {
  max-width: 364px;
}

.give__suptitle {
  color: #545be8;
}

.give__list {
  display: flex;
  gap: 40px;
}

.give__item {
  width: 267px;
  border-radius: 10px;
  background-color: #545be8;
  padding: 40px 24px 41px 16px;
}

.give__item-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #ffffff;
  margin-bottom: 8px;
}

.give__item-title {
  font-family: "Inter";
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  margin-bottom: 16px;
}

.give__item-text {
  max-width: 226px;
  font-size: 14px;
  line-height: 21px;
  color: #dddddd;
}

.give__navigation {
  margin-top: 24px;
  margin-left: 648px;
}

.slider-btns {
  display: flex;
  gap: 24px;
}

.slider-btns button {
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f0c932;
}

.statistics {
