.courses {
    padding-bottom: 0;
}

.courses__inner {
    display: flex;
    justify-content: space-between;
}
.courses__list {
    max-width: 696px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 42px;
    column-gap: 40px;
}
.courses__item {
    max-width: 328px;
    box-shadow: 0px 18.0585px 18.0585px rgba(0, 0, 0, 0.05);
}

.courses__content {
    max-width: 364px;
}

.courses__suptitle {
    max-width: 157px;
    color: #545be8;
}

.courses__text {
    margin-bottom: 24px;
}

.course {
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
}
.course__image {
    position: relative;
    max-width: 328px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.course__image::before {
    content: "";
    height: 66px;
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
}
.course__mark {
    position: absolute;
    z-index: 2;
    top: 22px;
    left: 0;
    font-weight: 700;
    font-size: 16.2527px;
    line-height: 24px;
    color: #ffffff;
    background-color: #545be8;
    padding: 9px 12px 10px;
}
.course__price {
    position: absolute;
    z-index: 2;
    right: 14px;
    bottom: 14px;
    font-weight: 700;
    font-size: 21.6702px;
    line-height: 33px;
    color: #ffffff;
}

.course__content {
    padding: 20px 14px 21px;
}

.course__conent-top {
    display: flex;
    justify-content: space-between;
}

.course__content-bottom {
    font-size: 16.2527px;
    line-height: 24px;
}

.course__content-bottom span:first-child {
    margin-right: 14px;
}

.course__title {
    font-weight: 700;
    font-size: 21.6702px;
    line-height: 33px;
    color: #141e32;
    margin-bottom: 7px;
}

.course__views {
    font-size: 12.641px;
    line-height: 19px;
    color: #545be8;
}
