.give {
    padding-left: 150px;
}

.give__inner {
    display: flex;
    gap: 24px;
}

.give__content {
    max-width: 364px;
}

.give__suptitle {
    color: #545be8;
}

.give__list {
    display: flex;
    gap: 40px;
}

.give__item {
    width: 267px;
    border-radius: 10px;
    background-color: #545be8;
    padding: 40px 24px 41px 16px;
}

.give__item-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #ffffff;
    margin-bottom: 8px;
}

.give__item-title {
    font-family: "Inter";
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    color: #ffffff;
    margin-bottom: 16px;
}

.give__item-text {
    max-width: 226px;
    font-size: 14px;
    line-height: 21px;
    color: #dddddd;
}

.give__navigation {
    margin-top: 24px;
    margin-left: 648px;
}

.slider-btns {
    display: flex;
    gap: 24px;
}

.slider-btns button {
    border: none;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f0c932;
}
