.statistics {
    background: linear-gradient(180deg, #545be8 0%, #272db0 100%);
    padding: 53px 0 82px;
}

.statistics__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.statistics__content {
    max-width: 457px;
    color: #dddddd;
}
.statistics__suptitle {
    color: #f0c932;
}
.statistics__title {
    color: #ffffff;
}

.statistics__text {
    margin-bottom: 40px;
}

.statistics__list {
    display: flex;
    justify-content: space-between;
}
.statistics__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Inter";
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
}

.statistics__item span:first-child {
    color: #f0c932;
}

.statistics__image-1 {
    right: 60px;
    top: -21px;
}

.statistics__image-2 {
    left: 21px;
    bottom: 36px;
}

.statistics__image-2 svg ellipse {
    fill: #FE753F;
}

.statistics__image-3 {
    right: -19px;
    bottom: 190px;
}

.statistics__image-4 {
    bottom: 150px;
    right: -35px;
}

.statistics__image-5 {
    top: 0;
    left: 69px;
}

.statistics__image-5 svg ellipse {
    fill: #FE753F;
}

.statistics__image-6 {
    top: 63px;
    left: 11px;
}

.statistics__image-6 svg ellipse {
    fill: #058FCB;
}

.statistics__image-7 {
    top: 26px;
    right: -36px;
}

.statistics__image-8 {
    left: 98px;
    bottom: -48px;
}

.statistics__image-8 svg path {
    stroke: #ffffff;
}
